import React, { Component } from "react";
import styled,{keyframes} from "styled-components";
// import {devices} from "../devices";


const slide = keyframes`
  from {
      opacity: 0;
      transform: translateY(20px);
  }
  to {
      opacity: 1;
      transform: translateY(0);
  }
`

const Wrapper = styled.div`
  position:relative;
  border-radius:2vw;
  border: solid 1px #d1119c;
  width:12vw;
  height:12vw;
  max-height:12vw;
  transition: transform .3s;
  cursor: pointer;
  text-align: center;
  vertical-align: middle;
  overflow:scroll;
  background-color:#ffffff;
  &:hover{
    transform: scale(1.04);

  }
  margin:0.8vw;

`;

const Title = styled.h3`
  font-size: 1.2vw
  margin: 0.2vw;
  font-family: "Nunito";
  font-weight: 700
  animation: ${slide} 2s ease;
`;

const Description = styled.p`
  font-size: 1.1vw;
  font-family: "Roboto";
  font-weight: 400;
`;

const Overlay = styled.div`

width:12vw;
height:12vw;
max-height:12vw;
transition: transform .3s;
cursor: pointer;
text-align: center;
vertical-align: middle;
overflow:scroll;
background:rgba(209,17,156,0.9);
position:absolute;
top:0;
left:0;
display: flex;
align-items: center;

`;

const FeatureButton = styled.button`
border-radius:.8vw;
border: solid 2px #ffffff;
background-color:#d1119c;
width:6vw;
height:3vw;
max-height:3vw;
transition: transform .3s;
cursor: pointer;
text-align: center;
vertical-align: middle;
overflow:scroll;
padding:0.5vw;
margin:auto;
font-size: 1.2vw
font-family: "Nunito";
font-weight: 700
color:#ffffff;
outline:none;

`;


export const FeatureType = {selectable: 1, selected: 2, disabled: 3};

class Feature extends Component {
  constructor(props) {
    super(props);
    this.state = {
      overlay:false
    };
  }

  render() {
    let overlay = null;
    if(!(this.props.featureType == FeatureType.disabled) && this.state.overlay){
      overlay = <Overlay>
        <FeatureButton onClick={()=>{this.setState({overlay:false}); this.props.clickHandler(this.props)}}>{this.props.featureType == FeatureType.selectable ? "Add" : "Remove"}</FeatureButton>
      </Overlay>
    }
    return (
      <Wrapper isDisabled={this.props.featureType == FeatureType.disabled} onMouseEnter={() => {this.setState({overlay:true})}} onMouseLeave={() => {this.setState({overlay:false})}}>
        <Title>{this.props.title}</Title>
        <Description>{this.props.description}</Description>
        {overlay}
      </Wrapper>
  )

    return null;
  }
}

export default Feature;
