import React, { Component } from "react";
import styled, {keyframes} from "styled-components";
import logo from '../drone.png';




const slide = keyframes`
  from {
      opacity: 0;
      transform: translateY(20px);
  }
  to {
      opacity: 1;
      transform: translateY(0);
  }
`

const StoryImage = styled.img`
  height:40vw;
  width:40vw;
  max-height:85vh;
  max-width:85vh;
  animation: ${slide} 2s ease;
`;

const StoryText = styled.h1`

`;


const StartButton = styled.button`
border-radius: 0.3vw
border: solid 1px #d1119c;
background-color:#ffffff;
height:3vw;
max-height:3vw;
transition: transform .3s;
cursor: pointer;
text-align: center;
vertical-align: middle;

&:hover{
  background-color:#d1119c;
  color:#ffffff;

}

font-size: 1.2vw
margin: auto;
font-family: "Nunito";
font-weight: 900
color:#d1119c;

outline:none;

`;

const Story = [
  { text: "This is a drone. It can fly high!", imgSrc: require('../story1.jpg') },
  { text: "It can fly over the trees and buildings!", imgSrc: require('../story2.jpg') },
  { text: "You can control it from below", imgSrc: require('../story3.jpg') },
  { text: "You can add cool superpowers and make it a dream drone!", imgSrc: require('../story4.jpg') }
];

var intervelRef = null;

const a = [
  <StoryImage src={require('../story1.jpg')} alt="logo" ></StoryImage>,
  <StoryImage src={require('../story2.jpg')} alt="logo" ></StoryImage>,
  <StoryImage src={require('../story3.jpg')} alt="logo" ></StoryImage>,
  <StoryImage src={require('../story4.jpg')} alt="logo" ></StoryImage>
]


export default class StartView extends Component {
  constructor(props) {
    super(props);
    this.state = {
      storyIndex: 0
    };
  }

  componentDidMount() {
    this.setState({storyIndex: 0});
    intervelRef = setInterval(this.runStorySequence, 5000)
  }

  render() {

    return (
      <div>
        {a[this.state.storyIndex]}
        <StoryText>{Story[this.state.storyIndex].text}</StoryText>
        <StartButton onClick={() => this.startClick()}>Get Started!</StartButton>
      </div>
    )
    return null;
  }

  runStorySequence = () => {
    var index = this.state.storyIndex;
    if( index < Story.length-1){
      index++;
      this.setState({storyIndex: index});
      
    }
    else(clearInterval(intervelRef))
  }


  startClick() {
    fetch('/drones/',
      {
        method: 'POST',
        body: JSON.stringify({
        }),
        headers: {
          'Accept': 'application/json, text/plain, */*',
          'Content-Type': 'application/json',
          'Mode': "CORS"
        }
      }).then(response => response.json())
      .then(data => {
        console.log(data);
        this.props.history.push('superpowers/' + data._id);
      }
      );

    alert("Successfully Created!");


  }

}

