import React, { Component } from "react";
import styled from "styled-components";
import Feature, { FeatureType as FeatureTypes } from "./Feature";
import NewStory from "./NewStory";
import { FiChevronLeft, FiChevronRight } from "react-icons/fi";
import logo from '../drone.png';

const Wrapper = styled.div`
  width:100%;
`;

const Header = styled.div`
  width:100%;
  height: 8vh;
  background-color: #d1119c;
  display:flex;
  justify-content:space-around;
  
`;

const HeaderText = styled.h2`
font-size:3.5vh;
width:80vw;
text-align:left;
font-family: 'Nunito';
font-weight:700;
color:#ffffff;
margin:auto 5vw;
letter-spacing: 0.5px
`;

const ContentWrapper = styled.div`
  margin:auto;
  position:relative;
  display:flex
  justify-content:space-around;
  height: 92vh;

`;

const DroneWrapper = styled.div`
  position:relative;
  margin:auto;
`;

const DroneImage = styled.img`
  height:40vw;
  width:40vw;
  max-height:85vh;
  max-width:85vh;
`;

const Overlay = styled.div`
position:absolute;
top:0;
left:0;
max-height:50vh;
max-width:30vh;
background-color:yellow;
`;

const FixedFeatureWrapper = styled.div`
position:absolute;
top: ${props => props.top}vw;
left: ${props => props.left}vw;
`;


const RowWrapper = styled.div`
  display:flex
  justify-content:space-around;
  flex-direction:column;

`;

const Row = styled.div`
  width:50vw;

`;

const RowHeader = styled.h3`
font-size: 1.5vw
margin: 0.2vw;
font-family: 'Nunito';
font-weight:700;
color: #d1119c;
`;

const Button = styled.button`
border-radius:0.5vw;
border: solid 1px #d1119c;
background-color:#ffffff;
min-width:7vw;
height:2.3vw;
max-height:2.3vw;
transition: transform .3s;
cursor: pointer;
text-align: center;
vertical-align: middle;

&:hover{
  background-color:#d1119c;
  color:#ffffff;
  border: solid 1px #ffffff;


}
font-size: 1.2vw
margin: auto;
font-family: "Nunito";
font-weight: 900
color:#d1119c;
outline:none;
`;



const FeatureSet = styled.div`
  width:50vw;
  display:flex
  justify-content:space-around;
  margin:10px;
`;

const OverlayFeatureSet = styled.div`
  width:25vw;
  display:flex
  justify-content:space-around;
  margin:10px;
`;

const FeatureWrapper = styled.div`
  margin-bottom: 5px;
`;

const LEFT_FACTOR = 26;
const TOP_FACTOR = 13;
const LEFT_0 = 6.5;
const TOP_0 = 6.5;

class AddStoryView extends Component {
  constructor(props) {
    super(props);
    this.state = {
      drone: null

    };
  }

  componentDidMount() {
    //fetch the drone data using id
    fetch("/drones/" + this.props.match.params.id)
      .then(response => response.json())
      .then(data => {
        var drone = data;
        if (!drone.mainStory) {
          fetch('/stories/',
            {
              method: 'POST',
              body: JSON.stringify({ text: "" }),
              headers: {
                'Accept': 'application/json, text/plain, */*',
                'Content-Type': 'application/json',
                'Mode': "CORS"
              }
            }).then(response => response.json())
            .then(data => {
              drone.mainStory = data;
              this.setState({ drone: drone })
              this.updateDrone();
            }
            );
        } else {
          this.setState({ drone: drone })
          this.updateDrone();
        }
      });
  }

  updateDrone() {
    fetch('/drones/' + this.state.drone._id,
      {
        method: 'PUT',
        body: JSON.stringify(this.state.drone),
        headers: {
          'Accept': 'application/json, text/plain, */*',
          'Content-Type': 'application/json',
          'Mode': "CORS"
        }
      }).then(response => response.json())
      .then(data => { }
      );
  }

  updateStory(story) {

    fetch('/stories/' + this.state.drone.mainStory._id,
      {
        method: 'PUT',
        body: JSON.stringify({ text: story }),
        headers: {
          'Accept': 'application/json, text/plain, */*',
          'Content-Type': 'application/json',
          'Mode': "CORS"
        }
      }).then(response => response.json())
      .then(data => {
      }
      );
  }

  render() {
    if (this.state.drone == null) return null;

    return (
      <Wrapper>
        <Header>
          <HeaderText>Step 2 of 3: Tell us a story about your drone</HeaderText>
          {this.renderButton()}
        </Header>
        <ContentWrapper>
          <DroneWrapper>
            <DroneImage src={logo} alt="logo" ></DroneImage>
            <Overlay>
              {/* <FeatureSet> */}
              {this.state.drone.features.map((item, key) =>
                <FixedFeatureWrapper top={TOP_0 + TOP_FACTOR * (key - (key % 2)) / 2} left={LEFT_0 + LEFT_FACTOR * (key % 2) / 2}>
                  <Feature title={item.title} description={item.description} index={key} featureType={FeatureTypes.disabled}></Feature>
                </FixedFeatureWrapper>
              )}
              {/* </FeatureSet> */}
            </Overlay>
          </DroneWrapper>

          <RowWrapper>
            <Row>
              <FeatureSet>
                <NewStory name={this.state.drone.name} story={this.state.drone.mainStory.text} callback={this.callbackhandler.bind(this)} />
              </FeatureSet>
            </Row>
          </RowWrapper>
        </ContentWrapper>
      </Wrapper>
    )
  }

  renderButton() {

    if (this.state.drone.name && this.state.drone.mainStory.text && this.state.drone.mainStory.text.length > 0) {
      return <Button onClick={() => { this.props.history.push('../select/' + this.state.drone._id) }}>Next</Button>
    }
    return null;
  }

  callbackhandler = (data) => {
    var drone = this.state.drone;
    drone.name = data.name;
    drone.mainStory.text = data.story;

    this.setState({ drone: drone })
    this.updateDrone();
    this.updateStory(data.story)
    

  }

}



export default AddStoryView;
