import React, { Component } from "react";
import styled from "styled-components";
import Button from "./Button";

// import {devices} from "../devices";


const Wrapper = styled.div`
  border-radius:1vw;
  border: solid 1px #d1119c;
  width:15vw;
  height:15vw;
  max-height:15vw;
  transition: transform .3s;
  cursor: pointer;
  text-align: center;
  vertical-align: middle;
  overflow:scroll;
  padding:0.5vw;
  background-color:#ffffff;

  &:hover{
    transform: scale(1.04);

  }
`;

const Title = styled.textarea`
  font-size: 1.2vw
  margin: 0.2vw;
  font-family: "Nunito";
  font-weight: 700;
  resize: none;
  border: none;
  overflow: auto;
  outline: none;
  text-align:center;
`;

const Description = styled.textarea`
  font-size: 1.2vw;
  font-family: "Roboto";
  font-weight: 400;
  resize: none;
  resize: none;
  border: none;
  overflow: auto;
  outline: none;
  text-align:center;
  min-height:40%;
`;

const AddButton = styled.button`
border-radius:.8vw;
border: solid 1px #d1119c;
background-color:#ffffff;
width:6vw;
height:3vw;
max-height:3vw;
transition: transform .3s;
cursor: pointer;
text-align: center;
vertical-align: middle;
padding:0.5vw;

&:hover{
  transform: scale(1.04);
  background-color:#d1119c;
  color:#ffffff;

}

font-size: 1.2vw
margin: 0.2vw;
font-family: "Nunito";
font-weight: 700
color:#d1119c;
outline:none;
`;

const TITLE_PLACEHOLDER = "Superpower Name";
const DESCRIPTION_PLACEHOLDER = "A little description of what it does";

class NewFeature extends Component {
  constructor(props) {
    super(props);
    this.state = {
      title: "",
      titlePlaceholder: TITLE_PLACEHOLDER,
      description: "",
      descriptionPlaceholder: DESCRIPTION_PLACEHOLDER
    };
  }

  saveNewFeature() {

  }

  render() {
    return (
      <Wrapper>
        <Title
          placeholder={this.state.titlePlaceholder}
          onClick={(ev) => { this.setState({ titlePlaceholder: "" }) }}
          onBlur={(ev) => { this.setState({ titlePlaceholder: TITLE_PLACEHOLDER }) }}
          value={this.state.title}
          onChange={(ev) => this.onTitleChange(ev)}>
        </Title>

        <Description
          placeholder={this.state.descriptionPlaceholder}
          onClick={(ev) => { this.setState({ descriptionPlaceholder: "" }) }}
          onBlur={(ev) => { this.setState({ descriptionPlaceholder: DESCRIPTION_PLACEHOLDER }) }}
          value={this.state.description}
          onChange={(ev) => this.onDescriptionChange(ev)}>
        </Description>

        {this.renderAddButton()}
      </Wrapper>
    )
    return null;
  }

  onTitleChange(ev) {
    this.setState({ title: ev.target.value });
  }

  onDescriptionChange(ev) {
    this.setState({ description: ev.target.value });
  }

  renderAddButton() {
    if (this.state.title.length > 0 && this.state.description.length > 0) {
      return <AddButton onClick={() => this.handleAddClick()}>Add</AddButton>
    }
    return null;
  }

  handleAddClick() {
    fetch('/features/',
      {
        method: 'POST',
        body: JSON.stringify(this.state),
        headers: {
          'Accept': 'application/json, text/plain, */*',
          'Content-Type': 'application/json',
          'Mode': "CORS"
        }
      }).then(response => response.json())
      .then(data => {
        this.props.clickHandler(data);
        this.clear();
      }
      );
  }

  clear() {
    this.setState({ title: "", description: "" })
  }
}

export default NewFeature;
